@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,700;1,400;1,700&family=Poppins:ital,wght@0,400;0,600;1,400;1,600&family=Source+Code+Pro:ital,wght@0,400;0,700;1,400;1,700&display=swap");
body {
  padding: 0 10px;
  color: var(--text-main);
  background: var(--background-body);
  text-rendering: optimizeLegibility;
}

button,
input,
textarea {
  transition: background-color var(--animation-duration) linear, border-color var(--animation-duration) linear, color var(--animation-duration) linear, box-shadow var(--animation-duration) linear, transform var(--animation-duration) ease;
}

.card {
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: .25rem;
}

.centered-content {
  display: flex;
  align-items: center;
  justify-content: center;
}

.card img:first-child {
  border-radius: calc(0.25rem - 1px) calc(0.25rem - 1px) 0px 0px;
}

.card .card-body {
  padding: 1.25rem;
}

code, kbd {
  background: var(--background-alt);
  color: var(--code);
  padding: 5px;
  border-radius: 6px;
}

pre > code {
  padding: 10px;
  display: block;
  overflow-x: auto;
}

/* GRID */
/* ROW */
.row {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 2%;
}

.col-mb-12 {
  grid-column: span 12;
}

.col-mb-11 {
  grid-column: span 11;
}

.col-mb-10 {
  grid-column: span 10;
}

.col-mb-9 {
  grid-column: span 9;
}

.col-mb-8 {
  grid-column: span 8;
}

.col-mb-7 {
  grid-column: span 7;
}

.col-mb-6 {
  grid-column: span 6;
}

.col-mb-5 {
  grid-column: span 5;
}

.col-mb-4 {
  grid-column: span 4;
}

.col-mb-3 {
  grid-column: span 3;
}

.col-mb-2 {
  grid-column: span 2;
}

.col-mb-1 {
  grid-column: span 1;
}

@media (min-width: 540px) {
  .col-12 {
    grid-column: span 12;
  }
  .col-11 {
    grid-column: span 11;
  }
  .col-10 {
    grid-column: span 10;
  }
  .col-9 {
    grid-column: span 9;
  }
  .col-8 {
    grid-column: span 8;
  }
  .col-7 {
    grid-column: span 7;
  }
  .col-6 {
    grid-column: span 6;
  }
  .col-5 {
    grid-column: span 5;
  }
  .col-4 {
    grid-column: span 4;
  }
  .col-3 {
    grid-column: span 3;
  }
  .col-2 {
    grid-column: span 2;
  }
  .col-1 {
    grid-column: span 1;
  }
}

button,
input[type="submit"],
input[type="button"],
input[type="checkbox"] {
  cursor: pointer;
}

input:not([type="checkbox"]):not([type="radio"]),
select {
  display: block;
}

input,
select,
button,
textarea {
  color: var(--form-text);
  background-color: var(--background-alt);
  font-family: inherit;
  font-size: inherit;
  margin-right: 6px;
  margin-bottom: 6px;
  padding: 10px;
  border: none;
  border-radius: 8px;
  outline: none;
}

button,
input {
  border-radius: 20px;
}

input:not([type="checkbox"]):not([type="radio"]):not([type="range"]),
select,
button,
textarea {
  -webkit-appearance: none;
}

textarea {
  margin-right: 0;
  width: 100%;
  box-sizing: border-box;
  resize: vertical;
}

button,
input[type="submit"],
input[type="reset"],
input[type="button"] {
  padding-right: 30px;
  padding-left: 30px;
  text-transform: uppercase;
  font-weight: 600;
  font-family: "Poppins";
  font-size: 90%;
  color: white;
  background: var(--links);
}

button:hover,
input[type="submit"]:hover,
input[type="button"]:hover {
  background: var(--button-hover);
}

input:focus,
select:focus,
button:focus,
textarea:focus {
  box-shadow: 0 0 0 2px var(--focus);
}

input:disabled,
select:disabled,
button:disabled,
textarea:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

::placeholder {
  color: var(--form-placeholder);
}

/* CONTAINERS */
.container {
  margin: 20px auto;
  max-width: 1000px;
  padding: 0 20px;
}

.container-sm {
  margin: 20px auto;
  width: 75%;
  max-width: 640px;
  padding: 0 20px;
}

/* HEADER */
.header {
  padding: 20px;
  text-align: center;
}

/* HERO */
/* CHANGE background-image & background-color ACCORDINGLY*/
.hero-image {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(https://i.imgur.com/tDTb9SG.png);
  background-color: #fff523;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 100%;
  position: relative;
}

.hero-text {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}

a {
  text-decoration: none;
  color: var(--links);
}

a:hover {
  text-decoration: underline;
}

img {
  max-width: 100%;
  height: auto;
}

hr {
  border: none;
  border-top: 1px solid var(--border);
}

table {
  border-collapse: collapse;
  margin-bottom: 10px;
  width: 100%;
}

td,
th {
  padding: 6px;
  text-align: left;
}

th {
  border-bottom: 1px solid var(--border);
}

tbody tr:nth-child(even) {
  background-color: var(--background-alt);
}

::-webkit-scrollbar {
  height: 10px;
  width: 10px;
}

html {
  scrollbar-color: var(--scrollbar-thumb) var(--background-alt);
  scrollbar-width: thin;
}

::-webkit-scrollbar-track {
  background: var(--background-alt);
  border-radius: 6px;
}

::-webkit-scrollbar-thumb {
  background: var(--scrollbar-thumb);
  border-radius: 6px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--scrollbar-thumb-hover);
}

::selection {
  background-color: var(--selection);
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.2);
}

.modal .modal-content {
  background-color: #fefefe;
  position: relative;
  margin: 10% auto;
  padding: 1rem 2rem;
  border-radius: 1rem;
  width: 30rem;
}

.modal .close {
  color: #aaa;
  position: absolute;
  top: 5px;
  left: 10px;
  font-size: 28px;
}

.modal .close .close:hover,
.modal .close .close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.navbar {
  background-color: white;
  height: 90px;
  box-shadow: 0px 3px 5px #8e8e8e33;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-top: .5rem;
  padding-bottom: .5rem;
  padding-right: 3rem;
  padding-left: 3rem;
}

.navbar-brand {
  padding-top: .3125rem;
  padding-bottom: .3125rem;
  margin-right: 1rem;
  font-size: 1.25rem;
  text-decoration: none;
  white-space: nowrap;
}

.navbar-brand img {
  vertical-align: middle;
  height: 40px;
}

.navbar-items {
  margin-left: auto;
}

.navbar-link {
  display: inline-block;
  font-family: "Poppins";
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0em;
}

.navbar-link a {
  text-decoration: none;
  color: black;
  padding-left: 5vw;
}

.navbar-dropdown {
  display: inline-block;
  padding-left: 5vw;
  font-family: "Poppins";
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0em;
}

.dropdown-button {
  border: none;
  cursor: pointer;
  background-color: white;
  font-family: "Poppins";
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0em;
}

.dropdown-items {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-items a {
  text-decoration: none;
  color: black;
  display: block;
  padding: 16px 16px;
}

.navbar-dropdown:hover .dropdown-items {
  display: block;
}

.dropdown-items a:hover {
  background-color: #f1f1f1;
}

table {
  width: 100%;
  margin-bottom: 1rem;
  border-collapse: collapse;
  text-align: left;
}

table td,
table th {
  padding: 0.75rem;
  border-top: 1px solid #dee2e6;
}

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  line-height: 1.7;
}

p {
  margin-bottom: 1rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 3rem 0 1.38rem;
  font-family: "Poppins", sans-serif;
  line-height: 1.3;
}

h1 {
  margin-top: 0;
  font-size: 2.488rem;
}

h2 {
  font-size: 2.074rem;
}

h3 {
  font-size: 1.728rem;
}

h4 {
  font-size: 1.44rem;
}

h5 {
  font-size: 1.2rem;
}

h6 {
  font-size: 1.1rem;
}

small {
  font-size: 0.833rem;
}

pre,
code,
kbd,
samp {
  font-family: "Source Code Pro", monospace;
  font-size: 90%;
}

h1,
h2,
h3,
h4,
h5,
h6,
strong {
  color: var(--text-bright);
}

h1,
h2,
h3,
h4,
h5,
h6,
b,
strong,
th {
  font-weight: 600;
}

blockquote {
  border-left: 4px solid var(--focus);
  margin: 1.5em 0em;
  padding: 0.5em 1em;
  font-style: italic;
}

blockquote > footer {
  margin-top: 10px;
  font-style: normal;
}

blockquote cite {
  font-style: normal;
}

address {
  font-style: normal;
}

